import {createStore} from 'redux';
function counter(state = 0, action) {
	switch(action.type){
		case 'INC':
					return state +1;
		case 'DESC':
					return state -1;
		default:
			return state;
	}
}
let store = createStore(counter);
store.subscribe(()=>
	console.log(store.getState())
);
store.dispatch({type: 'INC'});
store.dispatch({type: 'INC'});
store.dispatch({type: 'INC'});
store.dispatch({type: 'INC'});
store.dispatch({type: 'DESC'});
export default store;


